html,
body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}

* {
  box-sizing: border-box;
}

.app {
  font-family: "Oswald", sans-serif;

  text-align: center;

  width: 100%;
  min-height: 100%;

  padding: 10vh 5vw;
  padding-bottom: 5vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  background-color: #3f5d2b;
  background: url(https://images.unsplash.com/photo-1516528060732-e9b76180f7a0?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1050&q=80);
  background-repeat: no-repeat;
  background-size: cover;
}

.app,
.app * {
  color: white;
}

.app > :not(:last-child) {
  margin-bottom: 2rem;
}

.app > img {
  width: 7rem;
  height: 7rem;
  object-fit: cover;
  border-radius: 50%;
  border: 1rem solid rgba(0, 0, 0, 0.2);
  box-sizing: content-box;
}

.app > header {
  font-weight: 500;
  font-size: 2rem;
}

.app > ul {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
  max-width: 20rem;
}

.app > ul > li {
  margin-bottom: 1rem;
  width: 100%;
}

.app > ul > li > a {
  border-radius: 2rem;
  background: rgba(0, 0, 0, 0.2);
  text-decoration: none;
  display: block;
  padding: 1rem;
  transition: background-color 0.2s;
}

.app > ul > li > a:hover {
  background: rgba(0, 0, 0, 0.3);
}

.app > footer {
  margin-top: auto;
  width: 100%;
  max-width: 20rem;
}

.app > footer > ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-around;
}
